import React from 'react';

const Logo = () => {
  return (
    <a className="logo">
      <span className="logo__main">CRIOS</span>
      <span className="logo__secondary">Free PSD Template</span>
      <span className="logo__circle"></span>
    </a>
  );
};

export default Logo;


